
import { defineComponent, ref, computed, watch } from "vue";

export default defineComponent({
  setup() {
    const dec = ref();
    const bin = ref();
    const oct = ref();
    const hex = ref();

    watch(dec, () => {
      bin.value = parseInt(dec.value).toString(2);
      oct.value = parseInt(dec.value).toString(8);
      hex.value = parseInt(dec.value).toString(16);
    });
    return {
      dec,
      bin,
      oct,
      hex
    }
  }
});
